

#products{
    width: 100%;
    display:flex;
    flex-wrap: wrap;
    justify-content: space-around;
    background-color:transparent
    }
    
    .product{
        width:23%;
        flex-wrap: wrap;
        height: 850px;
        margin:20px 0px;
        border:2px solid grey;
        text-align: center;
        background-color: beige;
    }
    .product h3 {
        margin-top: -190px;
        height: 20%;
        overflow:hidden;
    }
    .product img {
        width:90%;
        height:50%
    }