body {
    margin: 0px;
  }
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "Poppins", sans-serif;
    background-image: url("https://s3.amazonaws.com/js30-cdn/snowy-mountains-kracked.jpg");
    height: 100vh;
  }
  
  .form {
    width: 40%;
    min-width: 250px;
    background-color: white;
    border-radius: 10px;
    border: 2px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 10px rgba(8, 7, 16, 0.6);
    padding: 30px 35px;
  }
  
  .form h3 {
    font-size: 32px;
    font-weight: 500;
    line-height: 42px;
    text-align: center;
  }
  
  .form label {
    display: block;
    margin-top: 30px;
    font-size: 16px;
    font-weight: 500;
  }
  .form input {
    display: block;
    height: 50px;
    width: 100%;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding: 0 10px;
    margin-top: 8px;
    font-size: 14px;
    font-weight: 300;
    outline: none;
  }
  .form ::placeholder {
    color: #ccc;
  }
  
  .form button {
    margin-top: 50px;
    width: 100%;
    background-color: #ccc;
    color: #080710;
    padding: 15px 0;
    font-size: 18px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
    outline: none;
    border: none;
    margin-bottom: 10px;
  }
  
  .form a {
    color: green;
  }
  .warning {
    background-color: red;
    color: white;
    padding: 5px 0px;
    margin-top: 20px;
    display: none;
    border-radius: 5px;
    text-indent: 10px;
  }
  
  .mailWarning {
    background-color: red;
    color: white;
    padding: 5px 0px;
    margin-top: 20px;
    display: none;
    border-radius: 5px;
    padding-right: 5px;
    text-indent: 10px;
  }
  
  .passwordWarning {
    background-color: red;
    color: white;
    padding: 5px 0px;
    margin-top: 20px;
    display: none;
    border-radius: 5px;
    padding-right: 5px;
    text-indent: 10px;
  }
  